.modal-wrapper{
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: rgba(155, 155, 155, .5);

	display: -ms-flexbox;

	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.modal-window{
	background: #FFF;
	padding: 2em;
	text-align: center;
}

.modal-shown{
	height: 100vh;
	overflow: hidden;
}


